	components: { uniCollapse, uniCollapseItem },
	data() {
		return {
			collData: [
				{
					id: 0,
					title: '考勤时间',
					content1: [
						{ title: `上班时间`, content: `周一,二,三,四,五 全天班次: 09:00-18:00  <br/> 休息时间: 12:30-13:00 <br/> 周六 ， 周日 休息 <br/> ` },
