					]
				},
				{
					id: 1,
					title: '考勤范围',
					content1: [{ title: '办公地点', content: '办公地点 <br/>  外勤打卡将会同步给主管' }]
				}
			]
		};
	},
	methods: {
		...mapMutations(['logout']),
		bindLogout() {
			this.logout();
			uni.reLaunch({
				url: '../login/login'
